import React from 'react'
import {NavLink} from 'react-router-dom'
import './style.scss'

const tabsData = [
  {id: 1, title: '首页', icon: 'iconfont icon-xiaoyuan-', path: '/home'},
  {id: 2, title: '发现', icon: 'iconfont icon-faxian', path: '/discover'},
  {id: 3, title: '我的', icon: 'iconfont icon-wode', path: '/mine'}
]
export default ()=>{
  return (
    <nav className="app-tabs">
    {
      tabsData.map(item=>(
        <NavLink className="tab" key={item.id} to={item.path}>
          <span className={item.icon}></span>
          <span className="text">{item.title}</span>
        </NavLink>
      ))
    }
    </nav>
  )
}
