import api from '../utils/api'
import Http from '../utils/Http'

// 获取关键字 (String)   page(1)  count(10)
export const requestKeyWord = async (text)=>{
  let result = await Http.fetch_get(api.KeyWord_Api+"?text="+text+"&page=0&count=10");
  if(result.retcode === 0){
    //发送成功
    return result.result;
  }else {
    //发送失败
    console.log("获得关键字失败....");
    return [];
  }
}

// 获取文章信息
// catalog_id = 249
export const getArtical = async (id)=>{
  let result = await Http.fetch_get(api.Articla_API+"&catalog_id="+id);
  if(result.resultcode === "200"){
    //发送成功
    return result.result;
  }else {
    //发送失败
    console.log("获得文章信息失败....");
    return [];
  }
}

// 获取题库的信息
export const getQuerytion = async ()=>{
  let result = await Http.fetch_get(api.Question_API);
  if(result.reason === "ok"){
    //发送成功
    return result.result;
  }else {
    //发送失败
    console.log("获得题库信息失败....");
    return [];
  }
}




export default {
  requestKeyWord,
  getArtical,
  getQuerytion
}