import React, { Component, lazy, Suspense } from 'react'
import {BrowserRouter as Router, Route, Redirect, Switch} from 'react-router-dom'
import Loading from './components/Loading/Loading'
import Tabs from './components/Tabs/Tabs'

const  Home = lazy(()=>import('./pages/home/home/home'));
const  Search = lazy(()=>import('./pages/home/home/children/search'));
const  QuestionDetail = lazy(()=>import('./pages/home/home/children/questionDetail'));
const Discover = lazy(()=>import('./pages/discover/discover/discover'));
const Mine = lazy(()=>import('./pages/mine/mine/mine'));
const Login = lazy(()=>import("./pages/login/login"));
const NotFind = lazy(()=>import('./pages/common/not-find/not-find'));
const classCom = lazy(()=>import("./pages/class/class/class"))
const Schedule = lazy(()=>import("./pages/schedule/schedule/schedule"))
const TextBook = lazy(()=>import("./pages/textbook/textbook/textbook"))
const Preview = lazy(()=>import("./pages/textbook/children/preview"))
const Mentoring = lazy(()=>import("./pages/mentoring/mentoring/mentoring"))
const Playing = lazy(()=>import("./pages/playing/paying"))

export default class App extends Component {

  render() {
    return (
      <Router>
        <Suspense fallback={<Loading/>}>
             
            {/* 根页面 */}
            <Switch>
              <Route path="/" exact render={()=>{
                return <Redirect to="/home"/>
              }}/>
              
              <Route path="/home" component={Home}/>
              <Route path="/discover" component={Discover}/>
              <Route path="/schedule" component={Schedule}/>
              <Route path="/class" component={classCom}/>
              <Route path="/textbook" component={TextBook}/>
              <Route path="/mentoring" component={Mentoring}/>
              <Route path="/mine" component={Mine}/>
              <Route path="/playing" component={Playing}/>
              <Route path="/login" component={Login}/>

              {/* 公共页面 */}
              <Route component={NotFind}/>
              
            </Switch>
            
            
            {/* 子页面 */}
            <Switch> 
              <Route path="/home/search" exact component={Search}/> 
              <Route path="/home/search/question" exact component={QuestionDetail}/>
              <Route path="/textbook/preview/:data" exact component={Preview}/>
            </Switch>

            {/* 底部 TabBar */}
            <Tabs/>
            
        </Suspense>
      </Router>
    )
  }
}
