import {getArtical,getQuerytion} from "../../../services/homeService"

const initialState = {
  playingList: []
}


// 同步action 
export const setArticalList = (val)=>({
  type: 'setArticalList',
  value: val
})

export const setQuestionList = (val)=>({
  type: 'setQuestionList',
  value: val
})


//异步action
export const requestAticalData = (id)=>async (dispatch)=>{
  console.log('请求文章数据.....');
  let response = getArtical(id);
  let action = setArticalList(response.data);
  dispatch(action);
}
export const requestQuestionData = ()=>async (dispatch)=>{
  console.log('请求题库数据.....');
  let response = getQuerytion();
  let action = setQuestionList(response);
  dispatch(action);
}


export default (state = initialState, action)=>{
  switch (action.type) {
    case 'setArticalList':
      return {
        ...state,
        articalList: action.value
      };
    case 'setQuestionList':
      return {
        ...state,
        questionList: action.value
      };
    default:
      return state;
  }
  
}